import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import {News} from "../../providers/news/models/news.model";
import {NewsProvider} from "../../providers/news/news";
import {HomePage} from "../home/home";

/**
 * Generated class for the NewsListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-news-list',
  templateUrl: 'news-list.html',
  providers : [NewsProvider]
})
export class NewsListPage {

  loaded = false;

  public posts: News[];

  constructor(private service: NewsProvider, private nav: NavController) {
    const me = this;

    setTimeout(() => {

      me.service.getPosts((posts) => {
        me.loaded = true;
        me.posts = posts;

      });
    }, 2500)
  }

  openHomePage() {
    this.nav.setRoot(HomePage);
  }

  public itemTapped(item) {
    console.log("Item tapped:", item)
    this.nav.push('NewsItemPage', {
      item: item
    });
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad NewsListPage');
  }

}



