import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import xml2js from 'xml2js';
import {News} from "./models/news.model";
import {AuthManagerProvider} from "../auth-manager/auth-manager";
import {LinkManagerProvider} from "../link-manager/link-manager";

/*
  Generated class for the NewsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NewsProvider {

  constructor(public http: Http, private auth: AuthManagerProvider, private linkManager: LinkManagerProvider) {
    console.log('Hello NewsProvider Provider');
  }

  public getPosts(callback){
    const me = this;

    console.log("Getting news for using clinic name: " + this.auth.clinicName);
    this.http.get(this.linkManager.getNews(this.auth.clinicName)).subscribe(response => {


      let parser = new xml2js.Parser({trim: true, explicitArray: true});
      let news = [];
      console.log(response.text());
      parser.parseString(response.text(), (err, result) => {
        const arr = result.rss.channel[0].item;
        console.log(arr);
        for(let i = 0; i < arr.length; i++) {
          let item = arr[i];
          let current = new News();
          current.body = me.first(item["description"]);
          current.title = me.first(item["title"]);
          current.date = me.first(item["pubDate"]).substr(0, me.first(item["pubDate"]).length - 13);
          current.thumb = item["media:thumbnail"] != null ? item["media:thumbnail"][0].$.url : null;
          current.picture = item["media:thumbnail"] != null ? item["media:thumbnail"][0].$.url : null;
          current.tags = ["Keine Tags vorhanden"];
          if (current.thumb != null && current.thumb.startsWith("//www.")) {
            current.thumb = "https:" + current.thumb;
          }
          if (current.picture != null && current.picture.startsWith("//www.")) {
            current.picture = "https:" + current.picture;
          }
          current.link = me.first(item["link"]);
          console.log(current);
          news.push(current);
        }

        callback(news);
      })


    });

  }

  private first(arr) {
    return arr[0];
  }
}



